$map-proportion: (16, 9);	// map size 16:9

@mixin map($proportion: $map-proportion, $width: 100%, $height: 0){
	.map {
		position: relative;
		width: $width;
		height: $height;
		@if ($height == 0 and length($proportion) >= 2) {
			padding-top: $width / nth($proportion, 1) * nth($proportion, 2);
		}

		&__msg {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			text-align: center;
		}

		@content;
	}
}